export function filterByDocumentData(data, documentType) {
  const URL_HEAD = process.env.REACT_APP_API_BASE_URL;
  let obj = data.filter((item) => item.document_type === documentType);
  const url = URL_HEAD + "/" + obj[0]?.document;
  return url;
}

export function disableDocViewer(data, documentType) {
  let obj = data.filter((item) => item.document_type === documentType);
  return obj[0]?.document?.length && obj[0]?.document !== "/media/False"
    ? true
    : false;
}

export function verifyStatusSwitch(status) {
  switch (status) {
    case STATUS_OPTIONS.UNVERIFIED.key:
      return STATUS_OPTIONS.UNVERIFIED.title;
    case STATUS_OPTIONS.VERIFIED.key:
      return STATUS_OPTIONS.VERIFIED.title;
    case STATUS_OPTIONS.PENDING.key:
      return STATUS_OPTIONS.PENDING.title;
    case STATUS_OPTIONS.NOT_UPLOADED.key:
      return STATUS_OPTIONS.NOT_UPLOADED.cs;
    default:
      return STATUS_OPTIONS.DEFAULT.title;
  }
}

export function splice(state, element) {
  let newArr = [...state];
  newArr.splice(newArr.indexOf(element), 1);
  return newArr;
}
